.elemsWrapper {
    position:relative;
    top:200px;
    width:100%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.designSection {
    position:relative;
    width:50%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;    
}
.element {
    width:200px;
    height:200px;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    border:2px solid purple;
    margin:25px;
    background-size:cover;
    background-position:center;
    cursor:pointer;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
}
.elemName {
    position:relative;
    top:84%;
    width:100%;
    height:16%;
    background-color:black;
    opacity:0.5;
    color:white;
    font-size:24px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight:600;
    text-align:center;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
}
.element:hover {
    -webkit-transform: scale(1.2) rotate(10deg);
        -ms-transform: scale(1.2) rotate(10deg);
            transform: scale(1.2) rotate(10deg);
}
.element:hover .elemName {
    opacity:1;
    height:20%;
    top:80%;
    font-size:28px;
}
@media (min-width: 768px) and (max-width: 1280px) {
    .elemName {
        font-size:20px;
    }
    .element:hover .elemName {
        font-size:24px;
    }
}
@media (min-width: 320px) and (max-width: 767px) {
    .elemName {
        font-size:16px;
    }
    .element:hover .elemName {
        font-size:20px;
    }
}


