.nav {
    position:absolute;
    width:100%;
    height:85px;
    -webkit-transition:height 0.5s;
    -o-transition:height 0.5s;
    transition:height 0.5s;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    z-index:1;
}
.nav:hover {
    height:170px;
}
.button {
    font-size:20px;
    margin-left:30px; margin-right:30px;
    z-index:1;
    cursor:pointer;
}
.name {
    height:24px;
    font-family:Arial, Helvetica, sans-serif;
    font-size:18px;
    font-weight:600;
    position:relative;
    top:50%;
    -webkit-transform:translateY(-50%);
        -ms-transform:translateY(-50%);
            transform:translateY(-50%);
    z-index:1;
}
.bar {
    position:relative;
    width:0;
    left:50%;
    height:5px;
    background-color:#666666;
    -webkit-transition:0.5s;
    -o-transition:0.5s;
    transition:0.5s;
    z-index:1;
}


