.links {
    position:fixed;
    height:50px;
    width:100%;
    bottom:1%;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.icon {
    height:30px;
    width:30px;
    margin:10px;
    border-radius:50%;
    cursor:pointer;
}
.icon:hover {
    width:50px;
    height:50px;
    margin:0px;
}


